@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    font: 400 14px Roboto, sans-serif;
    background-color: #dddddd;
    -webkit-font-smoothing: antialiased;
}

input, button, textarea {
    font: 400 18px Roboto, sans-serif;
}

button {
    cursor: pointer;
}

form input {
    width: 100%;
    height: 60px;
    color: #333333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 0 24px;
}

form textarea {
    width: 100%;
    min-height: 140px;
    color: #333333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 16px 24px;
    line-height: 24px;
}

.button {
    width: 100%;
    height: 60px;
    background: #e02041;
    border: 0;
    border-radius: 8px;
    color: #FFF;
    font-weight: 700;
    margin-top: 16px;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 60px;
    transition: -webkit-filter 0.5s;
    transition: filter 0.5s;
    transition: filter 0.5s, -webkit-filter 0.5s;
}

.button:hover {
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

.back-link{
    display: flex;
    align-items: center;
    margin-top: 40px;
    color: #41414d;
    font-size: 18px;
    text-decoration: none;
    font-weight: 500;
    transition: opacity 0.5s;
}

.back-link svg {
    margin-right: 8px;
}

.back-link:hover {
    opacity: 0.8;
}
.Logon {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: space-between;
}

.Logon section.form {
    width: 100%;
    max-width: 350px;
    margin-right: 30px;
}

.Logon section.form form {
    margin-top: 100px;
}

.Logon section.form form h1 {
    font-size: 32px;
    margin-bottom: 32px;
}
.Register {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: center;
}

.Register .container {
    width: 100%;
    padding: 96px;
    background: #f0f0f5;
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.1);
    border-radius: 8px;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Register .container section {
    width: 100%;
    max-width: 380px;
}

.Register .container section h1 {
    margin: 64px 0 32px;
    font-size: 32px;
}

.Register .container section p {
    font-size: 18px;
    color: #737380;
    line-height: 32px;
}

.Register .container form {
    width: 100%;
    max-width: 450px;
}

.Register .container form input {
    margin-top: 8px;
}

.Register .container form .input-group {
    display: flex;
}

.Register .container form .input-group input + input{
    margin-left: 8px;
}
.Profile {
    width: 100%;
    max-width: 1180px;
    padding: 0 30px;
    margin: 32px auto;
}

.Profile header {
    display: flex;
    align-items: center;
}

.Profile header span {
    font-size: 20px;
    margin-left: 24px;
}

.Profile header img {
    height: 64px;
}

.Profile header a {
    width: 260px;
    margin-left: auto;
    margin-top: 0;
}

.Profile header button {
    height: 60px;
    width: 60px;
    border-radius: 4px;
    border: 1px solid #dcdce6;
    background: transparent;
    margin-left: 16px;
    transition: border-color 0.2s;
}

.Profile header button:hover {
    border-color: #999;
}

.Profile h1 {
    margin-top: 80px;
    margin-bottom: 24px;
}

.Profile ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 24px;
    gap: 24px;
    list-style: none;
}

.Profile ul li {
    background: #FFF;
    padding: 24px;
    border-radius: 8px;
    position: relative;
}

.Profile ul li button {
    position: absolute;
    right: 24px;
    top: 24px;
    border: 0;
}

.Profile ul li button:hover {
    opacity: 0.8;
}

.Profile ul li strong {
    display: block;
    margin-bottom: 16px;
    color: #41415d;
}

.Profile ul li p + strong {
    margin-top: 32px;
}

.Profile ul li p {
    color: #737380;
    line-height: 21px;
    font-size: 16px;
}
